
import XHeader from 'vux/src/components/x-header/index.vue'


import scroll from "../scroll";
import { mapGetters } from "vuex";
export default {
components: {
XHeader,
scroll

},
computed: {
...mapGetters(["themesList", "themesTotal", "themeScrollTop"]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: "刷新完成"
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
created() {
this.$store.dispatch("getThemesList");
},
mounted() {




},
watch: {
themesList: function(val) {
this.themes = val;
},
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
}













},
activated() {
this.themes = this.themesList;
},
deactivated() {
this.themes = [];
},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "刷新完成",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "加载更多",
pullUpLoadNoMoreTxt: "没有更多数据了",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
page: 1,
num: 10,



themes: [],
noramlSrc: require("./shyk_bg.png")
};
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}

this.$store.dispatch("getThemesList");
this.$refs.scroll.forceUpdate();
this.page = 1;
}, 2000);
},
onPullingUp() {
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.page++;
this.$store.dispatch("getThemesList", { page: this.page }).then(() => {
if (this.themes.length >= this.themesTotal) {
this.$refs.scroll.forceUpdate();
}
});
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
},



























}
};



